.main-pagina-listado {
  background-color: rgb(105, 120, 255);
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main-pagina-listado section {
  width: 570px;
  text-align: center;
  padding: 20px;
  padding-bottom: 60px;
  background-color: rgb(27, 37, 128);
}
.main-pagina-listado section .section__contenedor-titulo-y-buscar {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
}
.main-pagina-listado section .section__contenedor-titulo-y-buscar h2 {
  font-family: "Staatliches";
  color: white;
  font-size: 18px;
  padding-top: 6px;
}
.main-pagina-listado section .section__contenedor-titulo-y-buscar .contenedor-titulo-y-buscar__filtrado {
  width: 160px;
  background-color: white;
  padding: 6px;
  border-radius: 0.6em;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 5px;
}
.main-pagina-listado section .section__contenedor-titulo-y-buscar .contenedor-titulo-y-buscar__filtrado b {
  font-family: "Staatliches";
  font-size: 16px;
}
.main-pagina-listado section .section__contenedor-titulo-y-buscar .contenedor-titulo-y-buscar__filtrado .filtrado__icono-buscar {
  width: 18px;
}
.main-pagina-listado section p {
  color: white;
  font-family: "Inter", sans-serif;
  font-size: 14px;
}
.main-pagina-listado section .section__contenedor-cryptos {
  padding-top: 30px;
  display: grid;
  grid-template-columns: 23% 23% 23%;
  grid-template-rows: 40% 40% 40%;
  align-items: center;
  justify-content: center;
}
.main-pagina-listado section .section__contenedor-cryptos .contenedor-cryptos__bitcoin {
  display: flex;
  align-items: center;
  gap: 3px;
}
.main-pagina-listado section .section__contenedor-cryptos .contenedor-cryptos__cardano {
  display: flex;
  align-items: center;
  gap: 3px;
}
.main-pagina-listado section .section__contenedor-cryptos .contenedor-cryptos__tether {
  display: flex;
  align-items: center;
  gap: 3px;
}
.main-pagina-listado section .section__contenedor-cryptos .contenedor-cryptos__ethereum {
  display: flex;
  align-items: center;
  gap: 3px;
}
.main-pagina-listado section .section__contenedor-cryptos .contenedor-cryptos__solana {
  display: flex;
  align-items: center;
  gap: 3px;
}
.main-pagina-listado section .section__contenedor-cryptos .contenedor-cryptos__bnb {
  display: flex;
  align-items: center;
  gap: 3px;
}
.main-pagina-listado section .section__contenedor-cryptos .contenedor-cryptos__litecoin {
  display: flex;
  align-items: center;
  gap: 3px;
}
.main-pagina-listado section .section__contenedor-cryptos .contenedor-cryptos__chainlink {
  display: flex;
  align-items: center;
  gap: 3px;
}
.main-pagina-listado section .section__contenedor-cryptos .contenedor-cryptos__usd-coin {
  display: flex;
  align-items: center;
  gap: 3px;
}
.main-pagina-listado section .section__contenedor-cryptos img {
  width: 42px;
}
.main-pagina-listado section .section__contenedor-cryptos p {
  display: inline-block;
}

